<template>
  <div v-html="yaml" class="yaml"/>
</template>

<script>
  import linkify from '@/utils/linkify';
  import yaml from 'yamljs';

  export default {
    props: {
      value: {
        type: null,
        required: true
      },
    },
    computed: {
      yaml() {
        return linkify(yaml.stringify(this.value, 2), 50);
      }
    }
  }
</script>

<style lang="scss">
  .yaml {
    white-space: pre;
  }
</style>



